@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");
@tailwind base;
@tailwind components;
body {
  @apply bg-purple-100 text-gray-700 flex flex-col min-h-screen;
}

@tailwind utilities;
input[type="number"],
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

h1 {
  @apply md:text-6xl text-3xl text-purple-900 font-bold my-6;
}

header {
  @apply shadow-lg mb-4;
}

header .nav-bar {
  @apply flex justify-between p-8;
}

header .nav-bar .menu-button {
  @apply md:h-12 md:w-12 h-8 w-8 hover:text-purple-700 transform duration-200 hover:scale-110 scale-100;
}

header .nav-bar .home-button {
  @apply font-logo md:text-6xl text-3xl hover:text-purple-700 transform duration-200 hover:scale-110 scale-100;
}

header .nav-bar .cart-button {
  @apply md:h-12 md:w-12 h-8 w-8 hover:text-purple-700 transform duration-200 hover:scale-110 scale-100;
}

main {
  @apply container mx-auto shadow-lg flex-grow mb-4 pb-8;
}

footer {
  @apply py-4 bg-white;
}

footer .footer-links {
  @apply flex justify-evenly items-center text-center my-4;
}

footer .footer-links a {
  @apply text-base font-light transform duration-200 hover:scale-110 scale-100;
}

footer .social-networks {
  @apply flex justify-evenly items-center;
}

footer .social-networks a {
  @apply transform duration-200 hover:scale-110 scale-100;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.75;
  }
  100% {
    opacity: 0.75;
  }
}

toast-element {
  @apply fixed rounded-lg bottom-12 z-10 bg-black left-0 right-0 mx-auto;
  @apply text-lg font-semibold text-white text-center p-4 w-10/12 md:w-2/5;
  @apply flex flex-row items-center;
  animation: fade 2s both 2 alternate linear;
}

toast-element[type="success"] {
  @apply bg-green-700;
}

toast-element[type="error"] {
  @apply bg-red-700;
}

toast-element p {
  @apply justify-self-center flex-grow;
}

spinbox-element {
  @apply relative;
}

spinbox-element input {
  @apply rounded-lg bg-purple-200 py-2 px-4 text-center font-bold appearance-none w-full;
}

spinbox-element input[type="number"] {
  -moz-appearance: textfield;
}

spinbox-element button:nth-of-type(1) {
  @apply absolute left-2 top-2;
}

spinbox-element button:nth-of-type(2) {
  @apply absolute right-2 top-2;
}

.teddy-card {
  @apply rounded-2xl shadow-md mt-8 max-w-xs bg-purple-200 h-72;
  @apply transform duration-200 hover:scale-110 scale-100;
}

.teddy-card:hover .teddy-card__price {
  @apply animate-bounce text-green-400;
}

.teddy-card__image {
  @apply shadow-md rounded-t-2xl h-3/4 w-full object-cover;
  @apply border-purple-300 border-b-2 border-solid;
}

.teddy-card__name {
  @apply px-4 font-medium text-lg pt-2;
}

.teddy-card__info {
  @apply px-4 flex justify-between items-center pb-2;
}

.teddy-card__description {
  @apply font-light;
}

.teddy-card__price {
  @apply font-bold text-lg text-green-500;
}

.product-list {
  @apply flex flex-wrap justify-start items-start;
}

.product-list li {
  @apply px-4;
}

.teddy {
  @apply grid md:grid-cols-2 md:grid-rows-1 grid-rows-2;
}

.teddy__image {
  @apply rounded-xl flex-shrink md:flex-shrink-0;
}

.teddy__info {
  @apply px-4;
}

.teddy__name {
  @apply font-semibold 2xl:text-2xl text-lg;
}

.teddy__description {
  @apply font-light 2xl:text-lg text-sm;
}

.teddy__color-label {
  @apply text-sm mr-4;
}

.teddy__color-select {
  @apply text-sm shadow-lg px-2 py-2 rounded-md border-purple-300 border-2 appearance-none w-36;
}

.teddy__color-select__container {
  @apply relative inline-block;
}

.teddy__color-select__arrow {
  @apply text-purple-900 absolute pointer-events-none;
  width: 1.5rem;
  right: 5%;
  top: 25%;
}

.teddy__color-select * {
  font-size: 1rem;
}

.teddy__shipping {
  @apply flex;
}

.teddy__shipping__image {
  @apply text-purple-900;
}

.teddy__shipping__text {
  @apply self-center pl-8;
}

.teddy__price {
  @apply font-bold text-green-700 text-right text-xl py-4;
}

.product-buttons spinbox-element {
  @apply mb-4 w-3/4 lg:w-1/2 lg:mt-4;
}

.add-to-cart-button {
  @apply bg-green-700 text-white py-2 px-8 text-center rounded-lg transform duration-200;
  @apply hover:scale-110 scale-100 font-bold;
}

.cart__item-list {
  @apply flex flex-col px-4 shadow-lg rounded-lg mb-4;
  min-height: 8rem;
}

.cart__item-list__title {
  @apply px-4 font-medium text-lg pt-2;
}

.cart__item-list__empty-text {
  @apply text-center w-full my-8;
}

.cart__total-price {
  @apply text-green-500;
}

.cart__total-price__container {
  @apply text-right font-semibold text-lg px-4;
}

.cart__order-button {
  @apply bg-green-700 text-white py-2 px-8 text-center rounded-lg font-bold;
  @apply transform duration-200 scale-100 hover:scale-110 inline-block;
}

.cart__order-button__container {
  @apply text-center my-8;
}

.cart-item {
  @apply flex justify-evenly my-2 flex-col md:flex-row shadow-lg p-2 rounded-lg;
}

.cart-item__info {
  @apply flex flex-row;
  max-width: 440px;
}

.cart-item__info__image {
  @apply w-1/3 md:w-1/4 object-cover rounded-lg;
}

.cart-item__info__text-container {
  @apply px-4 flex-grow;
}

.cart-item__info__name {
  @apply text-lg font-semibold;
}

.cart-item__info__color {
  @apply font-light;
}

.cart-item__info__price {
  @apply text-right;
}

.cart-item__info__price__text {
  @apply hidden md:inline;
}

.cart-item__total-price {
  @apply text-lg font-semibold text-green-500 text-right md:flex md:justify-end md:items-center;
}

@media (min-width: 768px) {
  .cart-item__total-price {
    min-width: 100px;
  }
}

.cart-item__buttons-container {
  @apply flex flex-wrap md:flex-nowrap justify-center items-center;
  min-width: 80px;
}

.cart-item spinbox-element {
  @apply relative inline-block my-4 w-2/3;
}

.progress-step {
  @apply flex flex-row justify-center items-center mx-4 mb-6;
}

.progress-step__step {
  @apply rounded-full bg-purple-200 w-12 h-12 flex flex-row justify-center items-center text-black z-10;
}

.progress-step__step[data-state="done"] {
  @apply bg-green-500 text-white;
}

.progress-step__path {
  @apply w-12 md:w-24 h-2 relative;
}

.progress-step__path::after {
  content: "";
  @apply absolute rounded-full bg-purple-200 -left-4 -right-4 h-full;
}

.progress-step__path[data-state="done"]::after {
  @apply bg-green-500;
}

@keyframes expend {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}

@keyframes swipe {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

.order-form {
  position: relative;
  animation: swipe 200ms ease-in-out both;
  animation-play-state: paused;
}

.order-form .personnal-info,
.order-form .pay-info {
  @apply grid grid-cols-2 px-8 max-w-max;
}

.order-form .personnal-info__container,
.order-form .pay-info__container {
  @apply lg:flex lg:flex-col lg:items-center;
}

.order-form .personnal-info label,
.order-form .pay-info label {
  @apply font-semibold block text-center mr-4 my-4;
}

.order-form .personnal-info input[type="text"],
.order-form .personnal-info input[type="email"],
.order-form .personnal-info input[type="month"],
.order-form .pay-info input[type="text"],
.order-form .pay-info input[type="email"],
.order-form .pay-info input[type="month"] {
  @apply block my-4 px-4 py-2 bg-purple-200 rounded-md;
}

.order-form .personnal-info input[type="text"]:not(:focus):invalid,
.order-form .personnal-info input[type="email"]:not(:focus):invalid,
.order-form .personnal-info input[type="month"]:not(:focus):invalid,
.order-form .pay-info input[type="text"]:not(:focus):invalid,
.order-form .pay-info input[type="email"]:not(:focus):invalid,
.order-form .pay-info input[type="month"]:not(:focus):invalid {
  @apply border-2 border-solid border-red-300;
}

.order-form .personnal-info input[type="text"]:not(:focus):invalid:not(:placeholder-shown),
.order-form .personnal-info input[type="email"]:not(:focus):invalid:not(:placeholder-shown),
.order-form .personnal-info input[type="month"]:not(:focus):invalid:not(:placeholder-shown),
.order-form .pay-info input[type="text"]:not(:focus):invalid:not(:placeholder-shown),
.order-form .pay-info input[type="email"]:not(:focus):invalid:not(:placeholder-shown),
.order-form .pay-info input[type="month"]:not(:focus):invalid:not(:placeholder-shown) {
  @apply bg-red-200;
}

.order-form .personnal-info input[type="text"]:valid,
.order-form .personnal-info input[type="email"]:valid,
.order-form .personnal-info input[type="month"]:valid,
.order-form .pay-info input[type="text"]:valid,
.order-form .pay-info input[type="email"]:valid,
.order-form .pay-info input[type="month"]:valid {
  @apply border-2 border-solid border-green-300;
}

.order-form .personnal-info {
  animation: expend 200ms ease-in-out reverse both;
  animation-play-state: paused;
}

.order-form .pay-info {
  animation: expend 200ms ease-in-out both;
  animation-play-state: paused;
  position: absolute;
  z-index: -1;
}

.order-form .next-button {
  @apply lg:flex lg:flex-col lg:items-center;
  @apply bg-green-700 text-white py-2 px-8 text-center rounded-lg font-bold;
  @apply transform duration-200 scale-100 inline-block my-4;
}

.order-form .next-button:disabled {
  @apply bg-gray-300 text-gray-600;
}

.order-form .next-button:not(:disabled) {
  @apply hover:scale-110;
}

.order-form .next-button__container {
  @apply flex flex-row justify-center;
}

@keyframes fade_full {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.order-confirmation {
  @apply hidden flex-col justify-center items-center absolute;
  animation: fade_full linear 200ms both paused;
}

.confirmation {
  @apply flex flex-col justify-center items-center;
}

.confirmation__order-id {
  @apply text-lg md:text-2xl text-center font-semibold;
}

.confirmation__text {
  @apply font-light text-center md:text-xl;
}
